<template>
    <FcPage :title="title" isBorder showLeft>
        <div class="addTeaConfirm">
            <div class="addTeaConfirm-content">
                <FcCell text="基本信息" isBold :icon="fileImg"></FcCell>
                <div class="addTeaConfirm-form">
                    <div class="addTeaConfirm-form-line"></div>
                    <FcForm ref="FcForm" :renderList="addFormRenderList" v-model="formData" @suffixIcon="handleClickSuffixIcon"></FcForm>
                </div>

            </div>
            <FcSubmit @back="handleBack" @submit="handleSubmit"></FcSubmit>
            <FcLoading :isShow="showLoading" text="正在获取位置信息..."></FcLoading>
        </div>
    </FcPage>
</template>

<script>
import FcCell from '@/components/cell/cell.vue'
import fileImg from '@/assets/enterprise/app/agriculturalManage/add/file.png'
import { getLocation, getWeChatSignData } from '@/utils/wechat'
import FcLoading from '@/components/loading/loading.vue'
import FcForm from '@/components/form/form.vue'
import FcSubmit from '@/components/submit/submit.vue'
import { getAddFormRenderList } from './config'
import { throttleFnc } from '@/utils/common'
import { enterpriseUserDataStorage } from '@/utils/storage'
export default {
    name: 'addTeaConfirm',

    components: {
        FcCell,
        FcForm,
        FcSubmit,
        FcLoading
    },

    data() {
        return {
            title: "",
            fileImg: fileImg,
            addFormRenderList: getAddFormRenderList(),
            userData: enterpriseUserDataStorage.getUserData(),
            formData: {},
            isReadyWeChat: false,
            showLoading: false,
            id: ""
        }
    },

    created() {
        this.initWeChat()
        const id = this.$route.query.id
        this.title = id ? '修改茶园确权' : '申请茶园确权'
        if (id) {
            this.id = id
            this.getTeaConfirmData()
        }
    },

    mounted() { },

    methods: {
        /**
            初始化微信sdk
            @param
            @return
        */
        initWeChat() {
            return new Promise((resolve, reject) => {
                getWeChatSignData(["getLocation"]).then(() => {
                    this.isReadyWeChat = true
                    resolve()
                }).catch(() => {
                    reject()
                })
            })
        },
        /**
            获取茶园确权数据
            @param 
            @return
        */
        getTeaConfirmData() {
            this.$axios({
                url: `/tea/auth/authorizeInfo/${this.id}`,
                method: "GET",
                data: {}
            }).then((res) => {
                const { teaName, teaArea, teaAddress, seleniumContent, personInCharge, phone } = res.data
                this.formData = { teaName, teaArea, teaAddress, seleniumContent, personInCharge, phone }
            })
        },
        /**
            点击返回的处理事件
            @param 
            @return
        */
        handleBack() {
            this.$router.back(-1)
        },

        /**
            处理点击suffixIcon事件
            @param { Object } data 子组件传输数据
            @return
        */
        handleClickSuffixIcon(data) {
            this.showLoading = true
            if (!this.isReadyWeChat) {
                this.initWeChat().then(() => {
                    this.handleGetLocation()
                }).catch(() => {
                    this.showLoading = false
                })
            } else {
                this.handleGetLocation()
            }
        },

        /**
            处理获取地理职位的方法
            @param 
            @return
        */
        handleGetLocation() {
            getLocation().then((res) => {
                this.$axios({
                    url: 'https://restapi.amap.com/v3/geocode/regeo',
                    method: "GET",
                    data: {
                        key: 'f751aff38707f53f57b61405db8abf0a',
                        location: `${res.longitude},${res.latitude}`,  
                        radius: 50
                    },
                    notCheck: true
                }).then((res) => {
                    this.showLoading = false
                    this.$set(this.formData, 'teaAddress', res.regeocode.formatted_address)
                }).catch(() => {
                    this.showLoading = false
                })
            }).catch(() => {
                this.showLoading = false
            })
        },
        /**
            点击提交的处理事件
            @param 
            @return
        */
        handleSubmit: throttleFnc(function (callback) {
            this.$refs.FcForm.validateForm().then((formData) => {
                if (this.id) {
                    this.$axios({
                        url: '/tea/auth/updateAuthorize',
                        data: {
                            ...formData,
                            tenantName: this.userData.tenantName,
                            id: this.id
                        }
                    }).then((res) => {
                        this.$toast("修改成功")
                        setTimeout(() => {
                            callback && typeof callback === 'function' && callback()
                            this.$router.back(-1)
                        }, 200)

                    }).catch(() => {
                        callback && typeof callback === 'function' && callback()
                    })
                } else {
                    this.$axios({
                        url: '/tea/auth/addAuthorize',
                        data: {
                            ...formData,
                            tenantName: this.userData.tenantName
                        }
                    }).then((res) => {
                        this.$toast("新增成功")
                        setTimeout(() => {
                            callback && typeof callback === 'function' && callback()
                            this.$router.back(-1)
                        }, 200)

                    }).catch(() => {
                        callback && typeof callback === 'function' && callback()
                    })
                }
            }).catch(() => {
                callback && typeof callback === 'function' && callback()
            })
        })
    },

}

</script>

<style scoped lang='scss'>
.addTeaConfirm {
    padding: 0.3rem 0.3rem 0;
    height: 100%;
    background: #f5f5f5;
}
.addTeaConfirm-content {
    background-color: #fff;
    /deep/ .title-text {
        color: #629b88;
    }
    .addTeaConfirm-form-line {
        margin: 0 0.3rem;
        height: 1px;
        background-color: #eee;
    }
}
</style>


